.wcf--mailchimp i,
.wcf--mailchimp svg {
  height: 1em;
  width: 1em;
}
.wcf--mailchimp .mailchimp-response-message {
  display: none;
  margin-bottom: 10px;
  padding: 10px 15px;
  border-radius: 5px;
}
.wcf--mailchimp .mailchimp-response-message.error {
  display: block;
  color: #842029;
  background-color: #f8d7da;
  border-color: #f5c2c7;
}
.wcf--mailchimp .mailchimp-response-message.success {
  display: block;
  color: #0f5132;
  background-color: #d1e7dd;
  border-color: #badbcc;
}
.wcf--mailchimp .mailchimp-response-message.warning {
  display: block;
  color: #664d03;
  background-color: #fff3cd;
  border-color: #ffecb5;
}
.wcf--mailchimp .wcf-mailchimp-form {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.wcf--mailchimp label {
  display: inline-block;
  margin-bottom: 10px;
}
.wcf--mailchimp .wcf-mc-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  line-height: 1;
  align-self: flex-end;
  border: 1px solid #ced4da;
  border-radius: 3px;
  background-color: #ced4da;
  color: #000;
  padding: 12px 15px;
}
.wcf--mailchimp .wcf-mc-button.icon-position-after i,
.wcf--mailchimp .wcf-mc-button.icon-position-after svg {
  order: 15;
}
.wcf--mailchimp.style-1 .input {
  display: flex;
}
.wcf--mailchimp.style-1 .input .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  background-color: #e9ecef;
}
.wcf--mailchimp.style-1 .input input,
.wcf--mailchimp.style-1 .input .icon {
  border: 1px solid #ced4da;
  outline: none;
  border-radius: 3px;
}
.wcf--mailchimp.style-1 .input.after .icon {
  order: 2;
  margin-left: -1px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.wcf--mailchimp.style-1 .input.after input {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.wcf--mailchimp.style-1 .input.before .icon {
  margin-right: -1px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.wcf--mailchimp.style-1 .input.before input {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.wcf--mailchimp.style-2 .input {
  position: relative;
}
.wcf--mailchimp.style-2 .input input {
  border: none;
  padding: 15px;
}
.wcf--mailchimp.style-2 .input .icon {
  position: absolute;
  left: 0;
  padding-top: 10px;
}
.wcf--mailchimp.style-2 .input-group-wrapper {
  display: flex;
  align-items: center;
  gap: 15px;
}