.wcf--mailchimp {
  i,
  svg {
    height: 1em;
    width: 1em;
  }

  .mailchimp-response-message {
    display: none;
    margin-bottom: 10px;
    padding: 10px 15px;
    border-radius: 5px;

    &.error {
      display: block;
      color: #842029;
      background-color: #f8d7da;
      border-color: #f5c2c7;
    }

    &.success {
      display: block;
      color: #0f5132;
      background-color: #d1e7dd;
      border-color: #badbcc;
    }

    &.warning {
      display: block;
      color: #664d03;
      background-color: #fff3cd;
      border-color: #ffecb5;
    }
  }

  .wcf-mailchimp-form {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }

  label {
    display: inline-block;
    margin-bottom: 10px;
  }

  .wcf-mc-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    line-height: 1;
    align-self: flex-end;
    border: 1px solid #ced4da;
    border-radius: 3px;
    background-color: #ced4da;
    color: #000;
    padding: 12px 15px;
    &.icon-position-after {
      i,
      svg {
        order: 15;
      }
    }
  }

  &.style-1 {
    .input {
      display: flex;

      .icon {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px;
        background-color: #e9ecef;
      }

      input,
      .icon {
        border: 1px solid #ced4da;
        outline: none;
        border-radius: 3px;
      }

      &.after {
        .icon {
          order: 2;
          margin-left: -1px;
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
        }

        input {
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }
      }

      &.before {
        .icon {
          margin-right: -1px;
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }

        input {
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
        }
      }
    }

  }

&.style-2 {
  .input {
    position: relative;

    input {
      border: none;
      padding: 15px;
    }

    .icon {
      position: absolute;
      left: 0;
      padding-top: 10px;
    }
  }

  .input-group-wrapper {
    display: flex;
    align-items: center;
    gap: 15px;
  }



}


}